import React, { useState } from "react";
import "./Carousel.css";

const Carousel = ({ data }) => {
  const [index, setIndex] = useState(0);
  
  const leftArrowClick = () => {
    index >= 1 ? setIndex(index - 1) : setIndex(data.length - 1);
  }

  const rightArrowClick = () => {
    index < data.length - 1 ? setIndex(index + 1) : setIndex(0);
  }

  return (
    <div className="custom-carosel">
      <h2 className="project-title">{data[index].title}</h2>
      <div className="img-wrapper">
        <img className="img-fluid" src={data[index].image} alt={data[0].alt}></img>
        <div className="left-arrow dark-arrow-container" onClick={leftArrowClick}>
          <i className="dark-arrow fas fa-2x fa-chevron-left"></i>
        </div>
        <div className="right-arrow dark-arrow-container" onClick={rightArrowClick}>
          <i className="dark-arrow fas fa-2x fa-chevron-right"></i>
        </div>
      </div>
      <div className="overview-text-container">
        <h3>Overview</h3>
        <p className="overview-text">
          {data[index].overview}
        </p>
        <div className="container">
          <div className="row">
            <div className="col">
              <a
                href={data[index].liveLink}
                className="live-link"
                target="_blank"
                rel="noreferrer"
              >
                <button className="btn btn-outline-light">
                  View Live Project
                </button>
              </a>
            </div>
            <div className="col">
              <a
                href={data[index].githubLink}
                className="github-link"
                target="_blank"
                rel="noreferrer"
              >
                <button className="btn btn-outline-light">
                  View Github Page
                </button>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Carousel;
